/**
 *   
 */
import React from 'react';
import { FilterSectionProps } from '../../../types/health';
import './styles.scss';

const FilterSection: React.FC<FilterSectionProps & { type?: 'checkup' | 'prescription' }> = ({
  filters,
  onFilterChange,
  disabled = false,
  type = 'checkup'
}) => {
  const currentYear = new Date().getFullYear();
  const years = Array.from({ length: 10 }, (_, i) => currentYear - i);

  const categories = [
    { value: 'all', label: '' },
    { value: 'basic', label: ' ' },
    { value: 'blood', label: ' ' },
    { value: 'urine', label: ' ' },
    { value: 'imaging', label: ' ' },
    { value: 'cancer', label: ' ' },
    { value: 'other', label: '' }
  ];

  const departments = [
    { value: 'all', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' }
  ];

  return (
    <div className={`filter-section ${disabled ? 'filter-section--disabled' : ''}`}>
      <div className="filter-section__header">
        <h3 className="filter-section__title"></h3>
        <button 
          className="filter-section__reset"
          onClick={() => onFilterChange({
            year: 0,
            category: 'all',
            searchTerm: '',
            hospitalName: '',
            startDate: '',
            endDate: '',
            department: ''
          })}
          disabled={disabled}
        >
          
        </button>
      </div>

      <div className="filter-section__content">
        {/*   */}
        <div className="filter-group">
          <label className="filter-label"></label>
          <select
            className="filter-select"
            value={filters.year || ''}
            onChange={(e) => onFilterChange({ 
              ...filters, 
              year: e.target.value ? parseInt(e.target.value) : 0 
            })}
            disabled={disabled}
          >
            <option value=""></option>
            {years.map(year => (
              <option key={year} value={year}>
                {year}
              </option>
            ))}
          </select>
        </div>

        {type === 'checkup' ? (
          <>
            {/*   */}
            <div className="filter-group">
              <label className="filter-label"> </label>
              <select
                className="filter-select"
                value={filters.category || 'all'}
                onChange={(e) => onFilterChange({ 
                  ...filters, 
                  category: e.target.value 
                })}
                disabled={disabled}
              >
                {categories.map(category => (
                  <option key={category.value} value={category.value}>
                    {category.label}
                  </option>
                ))}
              </select>
            </div>
          </>
        ) : (
          <>
            {/*    () */}
            <div className="filter-group">
              <label className="filter-label"></label>
              <input
                type="date"
                className="filter-input"
                value={filters.startDate || ''}
                onChange={(e) => onFilterChange({ 
                  ...filters, 
                  startDate: e.target.value 
                })}
                disabled={disabled}
              />
            </div>

            <div className="filter-group">
              <label className="filter-label"></label>
              <input
                type="date"
                className="filter-input"
                value={filters.endDate || ''}
                onChange={(e) => onFilterChange({ 
                  ...filters, 
                  endDate: e.target.value 
                })}
                disabled={disabled}
              />
            </div>

            {/*   */}
            <div className="filter-group">
              <label className="filter-label"></label>
              <select
                className="filter-select"
                value={filters.department || 'all'}
                onChange={(e) => onFilterChange({ 
                  ...filters, 
                  department: e.target.value === 'all' ? '' : e.target.value 
                })}
                disabled={disabled}
              >
                {departments.map(dept => (
                  <option key={dept.value} value={dept.value}>
                    {dept.label}
                  </option>
                ))}
              </select>
            </div>
          </>
        )}

        {/*   */}
        <div className="filter-group">
          <label className="filter-label"></label>
          <input
            type="text"
            className="filter-input"
            placeholder=" "
            value={filters.hospitalName || ''}
            onChange={(e) => onFilterChange({ 
              ...filters, 
              hospitalName: e.target.value 
            })}
            disabled={disabled}
          />
        </div>

        {/*   */}
        <div className="filter-group">
          <label className="filter-label"></label>
          <input
            type="text"
            className="filter-input"
            placeholder=" "
            value={filters.searchTerm || ''}
            onChange={(e) => onFilterChange({ 
              ...filters, 
              searchTerm: e.target.value 
            })}
            disabled={disabled}
          />
        </div>
      </div>
    </div>
  );
};

export default FilterSection;
